<template>
  <div class="setting">
    <div class="box">
      <h1 class="store-name cb">青浦一店 <em @click="showPicker">营业中</em></h1>
      <div class="link">
        <router-link to="/storeManage">门店管理</router-link>
        <router-link to="/printerSetting">打印机与接单设置</router-link>
        <router-link to="/accountSafe">账号与安全</router-link>
      </div>
    </div>
    <my-menu />
  </div>
</template>

<script>
import myMenu from '@/components/menu'

export default {
  components: {
    myMenu
  },
  data () {
    return {
      storeStatus: [{ text: '营业中', value: '营业中' }, { text: '立即关店', value: '立即关店' }]
    }
  },
  methods: {
    showPicker () {
      if (!this.picker) {
        this.picker = this.$createPicker({
          data: [this.storeStatus],
          onSelect: this.selectHandle
        })
      }
      this.picker.show()
    },
    selectHandle (selectedVal, selectedIndex, selectedText) {
      this.$createDialog({
        type: 'warn',
        content: `Selected Item: <br/> - value: ${selectedVal.join(', ')} <br/> - index: ${selectedIndex.join(', ')} <br/> - text: ${selectedText.join(' ')}`,
        icon: 'cubeic-alert'
      }).show()
    }
  }
}
</script>

<style lang="stylus" scoped>
.setting {
  .store-name {
    font-size: 20px;
    padding-left: 30px;
    line-height: 60px;
    border-bottom: 1px solid #ccc;
    em {
      display: block;
      padding-right: 30px;
      font-size: 15px;
      float: right;
      background: url(../assets/img/bottom-arrow.png) no-repeat 45px center;
      background-size: auto 40%;
    }
  }
  .link {
    padding: 0 20px;
    margin-top: 50px;
    a {
      display: block;
      padding-left: 20px;
      line-height: 55px;
      border-bottom: 1px solid #ccc;
      background: url(../assets/img/right-arrow2.png) no-repeat right center;
      background-size: auto 55%;
      color: #000;
    }
  }
}
</style>



